<template>
	<view class="center">
		<u-navbar :is-back="false"  title="" title-color="#000000" :background="background">
			<view class="slot-wrap">
				<text class="iconfont iconsaoyisao" @tap="scan"></text>
				<text>个人中心</text>
				<text class="iconfont iconxiaoxi" @tap="notice"></text>
			</view>
		</u-navbar>
		<view class="logo" :hover-class="!login ? 'logo-hover':''" @touchstart="touchLogo()" @touchend='touchedLogo()' :class="boolean?'bgColor':''">
			<image class="logo-img" :src="login ? uerInfo.avatarUrl :avatarUrl"></image>
			<view class="logo-title" v-if="!login" @tap="goLogin">
				<text class="uer-name">您未登录</text>
				<text class="go-login navigat-arrow" >&#xe65e;</text>
			</view>
			<view class="logo-title" v-else @tap="amendInfor">
				<view class="logoName">
					<text class="uer-name">{{uerInfo.name}}</text>
					<view class="docInfor">
						<text>{{uerInfo.department}}</text>
						<text>|</text>				
						<text>{{uerInfo.hospital}}</text>
					</view>
				</view>
				<text class="go-login navigat-arrow" >&#xe65e;</text>
			</view>
		</view>
		<view class="center-list">
			<view class="center-list-item" @tap='enterPage(index)' v-for="(item,index) in listTop" :key='index' @touchstart="touchS(index)" @touchend='touchE(index)' :class="boolcolumn===index?'bgColor':''">
				<text class="iconfont list-icon" :class="item.icon"></text>
				<text class="list-text">{{item.title}}</text>
				<text class="navigat-arrow">&#xe65e;</text>
			</view>
		</view>
		<view class="center-list">
			<view class="center-list-item" @tap='enterPageBottom(index)' v-for="(item,index) in listBottom" :key='index'  @touchstart="touchSB(index)" @touchend='touchEB(index)' :class="boolcolumnB===index?'bgColor':''">
				<text class="iconfont list-icon" :class="item.icon"></text>
				<text class="list-text">{{item.title}}</text>
				<text class="navigat-arrow">&#xe65e;</text>
			</view>
			<view class="center-list-item" @tap="aboutApp">
				<text class="list-icon">&#xe614;</text>
				<text class="list-text">关于应用</text>
				<text class="navigat-arrow">&#xe65e;</text>
			</view>
		</view>
		<button class="exitBtn btn" v-if="login" @tap="switchLogin">切换账号</button>
		<button class="exitBtn" v-if="login" @tap="exitLogin">退出登录</button>
	</view>
</template>

<script>
	var loginRes;
	export default {
		data() {
			return {
				login: false,
				boolean:false,//头像一栏触摸样式
				boolcolumn:'',//顶部column，要设置为===，不然默认为0
				boolcolumnB:'',//底部column
				avatarUrl: "../../static/image/person.png",
				uerInfo: {},
				background: {
					backgroundColor: '#ffffff',
				},
				listTop:[
					{icon:'iconfangfaku',title:'我的手术方法库'},
					{icon:'icondingdanguanli',title:'我的订单管理'},
					{icon:'iconshebeiguanli',title:'我的设备管理'},
				],
				listBottom:[
					{icon:'iconzhanghaoanquan',title:'账号安全'},
					{icon:'icontongyong',title:'通用'},
					{icon:'iconguanyushuyi',title:'关于树蚁'},
				]
			}
		},
		methods: {
			// 扫一扫
			scan(){
				uni.scanCode({
					success(e) {
						console.log(e)
						console.log(e.errMsg)
						// 设置为跳页面测试
						// var url='https://www.baidu.com'
							// url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+url,
						// uni.navigateTo({
						// 	url:'/pages/deviceManage/addDevice?deviceCode='+e.result,
						// 	animationType:'pop-in'
						// })	
					},
					fail(e){
					// 	console.log(e)
						uni.showToast({
							title:'fail',
							icon:'none'
						})
					},
				})
			},
			// 消息
			notice(){},
			touchLogo(){//头像一栏
				this.boolean=true
			},
			touchedLogo(){//头像一栏
				this.boolean=false
			},
			touchS(index){//顶部column
				this.boolcolumn=index
			},
			touchE(index){//顶部column
				this.boolcolumn=''
			},
			touchSB(index){//底部column
				this.boolcolumnB=index
			},
			touchEB(index){//底部column
				this.boolcolumnB=''
			},
			// 登录
			goLogin() {
				console.log(!this.login)
				if (!this.login) {//false未登录
					console.log("点击前往登录")
					uni.navigateTo({
						url:"/pages/login/tellogin?pages="+'/pages/personcenter/personcenter'+'&pagetype='+1,
						animationType:'pop-in',
						success(e){},
						fail(error) {}
					})
				}
			},
			// 修改个人资料
			amendInfor(){
				uni.navigateTo({
					url:'/pages/PersonalInfor/PersonalInfor'
				})
			},
			// 我的设备管理
			enterPage(index){
				if(index==0){//我的手术方法库
					// uni.switchTab({
					// 	// 进入我的手术方法库
					// 	url:'/pages/surgicalData/surgicalData?mySurcigal='+0
					// })
				}
				if(index==1){//我的订单管理
					
				}
				if(index==2){//我的设备管理
					uni.navigateTo({
						url:'/pages/deviceManage/deviceManage'
					})
				}	
			},
			enterPageBottom(index){
				if(index==0){//账号安全
					
				}
				if(index==1){//通用
					
				}
				if(index==2){//关于树蚁
				}	
			},
			// 关于应用
			aboutApp(){
				// #ifdef APP-PLUS
					plus.runtime.getProperty(plus.runtime.appid,(infor)=>{
						 uni.showToast({
							title:'当前版本'+infor.version,
							icon:'none'
						 })
					})
				// #endif
			},
			// 切换账号
			switchLogin(){
				this.$myRequest({
					url:'/login/loginOut'
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.login=false
						uni.clearStorageSync();//清除所有缓存
						uni.navigateTo({
							url:'/pages/login/tellogin',
						})
					}else{
						uni.showToast({
							title:res.data.object
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 退出登录
			exitLogin(){
				this.$myRequest({
					url:'/login/loginOut'
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.login=false
						uni.clearStorageSync();//清除所有缓存
						uni.showToast({
							title:res.data.object
						})
					}else{
						uni.showToast({
							title:res.data.object
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		// 获取个人信息数据,判断是否登陆
		// onLoad(options) {//登陆的校验,用于其他页面的直接登陆
		// 	loginRes=this.checkLogin('/pages/personcenter/personcenter',1);//返回的页面和类型 navigate,switchTab
		// 	console.log(loginRes)//如果验证已经登陆了，则返回本地存储的数据
		// 	if(!loginRes){//没有登陆
		// 		return;
		// 	}
		// }
		onShow() {
			console.log(uni.getStorageSync('token'))
			if(uni.getStorageSync('token')){//token存在
				this.login=true
				this.$myRequest({
					url:'/doctor/getDoctor'
				}).then(res=>{
					console.log(res)
					var data=res.data
					this.uerInfo={
						name:data.name,
						hospital:data.hospital,
						department:data.departmentName,
						avatarUrl:data.avatarLinks||this.avatarUrl
					}
				}).catch(err=>{ 
					console.log(err)
				})
				console.log(this.uerInfo)
			}
		},
		onLoad() {
			console.log('加载一次')			
		}
	}
</script>

<style scoped>
	@font-face {
		font-family: texticons;
		font-weight: normal;
		font-style: normal;
		src: url('https://at.alicdn.com/t/font_984210_5cs13ndgqsn.ttf') format('truetype');
	}
	.slot-wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		/* 如果让slot内容占满整个导航栏的宽度 */
		flex: 1;
		/* 如果让slot内容与导航栏左右有空隙 */
		padding: 0 30rpx;
	}
	.slot-wrap uni-text:nth-child(2){
		color: #000000;
		font-family: PingFang-SC-Bold;
		font-size: 30rpx;
		font-weight: bold;
	}
	page,view {
		display: flex;
	}
	page {
		background-color: #f8f8f8;
	}
	.center {
		flex-direction: column;
	}
	.logo {
		width: 750rpx;
		height: 186rpx;
		padding: 30rpx;
		box-sizing: border-box;
		background-color: #FFFFFF;
		flex-direction: row;
		align-items: center;
	}
	.logo-hover {
		opacity: 0.8;
	}
	.logo-img {
		width: 126rpx;
		height: 126rpx;
		border-radius: 126rpx;
	}
	.logo-title {
		flex: 1;
		align-items: center;
		justify-content: space-between;
		flex-direction: row;
		margin-left: 20rpx;	
	}
	.logoName{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.docInfor{
		font-size: 24rpx;
	}
	.docInfor uni-text:nth-child(2){
		margin: 0 10rpx;
	}
	.uer-name {
		height: 60rpx;
		line-height: 60rpx;
		font-size: 30rpx;
		font-weight: bold;
		font-family: PingFang-SC-Bold;
		color: #000000;
	}
	.go-login.navigat-arrow {
		font-size: 30rpx;
		color: #000000;
	}
	.center-list {
		background-color: #FFFFFF;
		margin-top: 20rpx;
		width: 750rpx;
		flex-direction: column;
	}
	.center-list-item {
		box-sizing: border-box;
		flex-direction: row;
		padding: 40rpx 30rpx;
		border-bottom: 1px solid #EEEEEE;
		align-items: center;
	}
	.list-icon {
		width: 40rpx;
		font-size: 34rpx;
		color: #343434;
		text-align: center;
		font-family: texticons;
		margin-right: 20rpx;
	}
	.list-text {
		font-size: 30rpx;
		color: #000000;
		font-family: PingFang-SC-Regular;
		flex: 1;
		text-align: left;
	}
	.navigat-arrow {
		font-size: 34rpx;
		color: #333333;
		text-align: right;
		font-family: texticons;
	}
	.exitBtn{
		margin-bottom: 20rpx;
		width: 100%;
		font-size: 28rpx;
		background-color: #FFFFFF;
	}
	.btn{
		margin-top: 20rpx;
	}
	.bgColor{
		background-color: #F8F8F8;
	}
</style>